import type {
  GraphId,
  GraphSpec,
  InputId,
  InputVarId,
  OutcomeSpec,
  OutputVarId,
  PolicyGroupId
} from '@climateinteractive/sim-core'
import { assertNever } from '../_shared'
import {
  modelVersion,
  inputSpecs,
  graphSpecs,
  policyGroupSpecs,
  assumptionGroupSpecs,
  outcomeSpecs
} from './generated/config-specs'
import type { AssumptionGroupSpec } from './assumptions'
import type { InputSpec } from './inputs'
import type { PolicyGroupSpec } from './policies'

/**
 * Exposes all the configuration that can be used to build a user
 * interface around the En-ROADS model.
 */
export class Config {
  /**
   * @hidden This is intended for use by `getDefaultConfig` only.
   *
   * @param modelVersion The model version string.
   * @param inputs The available input specs; these are in the order expected by the model.
   * @param graphs The available graph specs.
   * @param policyGroups The available policy group specs.
   * @param assumptionGroups The available assumption group specs.
   * @param outcomes The available outcome specs.
   */
  constructor(
    public readonly modelVersion: string,
    public readonly inputs: ReadonlyMap<InputId, InputSpec>,
    public readonly graphs: ReadonlyMap<GraphId, GraphSpec>,
    public readonly policyGroups: ReadonlyMap<PolicyGroupId, PolicyGroupSpec>,
    public readonly assumptionGroups: ReadonlyArray<AssumptionGroupSpec>,
    public readonly outcomes: ReadonlyArray<OutcomeSpec>
  ) {}

  /**
   * Return the set of output variables that are needed for reference data.  This
   * includes output variables that appear with a "Ref" dataset in one or more
   * graph specs, as well as variables used for outcomes.
   */
  getRefOutputs(): Set<OutputVarId> {
    // Gather the set of output variables that appear with a "ref" dataset
    // in one or more graph specs
    const refVarIds: Set<OutputVarId> = new Set()
    for (const graphSpec of this.graphs.values()) {
      for (const dataset of graphSpec.datasets) {
        if (dataset.externalSourceName === 'Ref') {
          refVarIds.add(dataset.varId)
        }
      }
    }

    // Include output variables that are used for outcomes
    for (const outcomeSpec of this.outcomes.values()) {
      if (outcomeSpec.calculation === 'baseline-minus-current') {
        refVarIds.add(outcomeSpec.varId)
      }
    }

    return refVarIds
  }

  /**
   * Return the set of input variables that are needed for special model runs
   * to compute damage and social cost of carbon.  This currently only includes
   * assumptions and inputs in the Population and Economic Growth policy groups.
   */
  getDamageInputs(): Set<InputVarId> {
    const inputIds: Set<InputVarId> = new Set()

    const addInput = (inputId: InputId) => {
      const input = this.inputs.get(inputId)
      switch (input.kind) {
        case 'slider':
          inputIds.add(input.varId)
          break
        case 'switch':
          inputIds.add(input.varId)
          input.slidersActiveWhenOff.forEach(addInput)
          input.slidersActiveWhenOn.forEach(addInput)
          break
        default:
          assertNever(input)
      }
    }

    // Include assumptions
    for (const group of this.assumptionGroups) {
      group.inputIds.forEach(addInput)
    }

    // Include inputs in the Population policy group
    const popGroup = this.policyGroups.get('populationView')
    popGroup.inputIds.forEach(addInput)

    // Include inputs in the Economic Growth policy group
    const econGroup = this.policyGroups.get('economicGrowthView')
    econGroup.inputIds.forEach(addInput)

    return inputIds
  }
}

/**
 * Return the default configuration for the included model instance.
 */
export function getDefaultConfig(): Config {
  // Convert the arrays from `config-specs.ts` to maps
  const inputs: Map<InputId, InputSpec> = new Map(inputSpecs.map(spec => [spec.id, spec]))
  const graphs: Map<GraphId, GraphSpec> = new Map(graphSpecs.map(spec => [spec.id, spec]))
  const policyGroups: Map<PolicyGroupId, PolicyGroupSpec> = new Map(
    policyGroupSpecs.map(spec => [spec.id, spec])
  )

  return new Config(modelVersion, inputs, graphs, policyGroups, assumptionGroupSpecs, outcomeSpecs)
}
