import type { InputValue, ModelRunner, Outputs, Series } from '@sdeverywhere/runtime'
import { createSynchronousModelRunner } from '@sdeverywhere/runtime'

import { spawnAsyncModelRunner } from '@sdeverywhere/runtime-async'

import type { InputId, OutputVarId } from '@climateinteractive/sim-core'

import { getDefaultConfig } from '../config'

import type { ModelContext } from './context'
import type { Input } from './inputs'
import { createSimpleInputValue } from './inputs'
import { createDefaultOutputs } from './outputs'
import type { RefDataMap, RefScenario } from './ref-data'
import { ModelScheduler } from './scheduler'

import loadWasm from './generated/enroads'
import workerJs from './generated/worker.wjs?raw'

/**
 * High-level interface to the runnable En-ROADS model.  This is built on top
 * of `WasmModel` but presents a simplified interface that makes it easier
 * to work with the En-ROADS model in an application.
 *
 * When one or more input values are changed, this class will schedule a model
 * run to be completed as soon as possible.  When the model run has completed,
 * the output data will be saved (accessible using the `getSeriesForVar` function),
 * and `onOutputsChanged` is called to notify that new data is available.
 *
 * It is recommended to use `createAsyncModel` to create a `Model` instance that
 * runs the Wasm model asynchronously in a worker thread.  For less common use
 * cases, it is also possible to run the model synchronously on the JavaScript
 * thread, though this is not recommended (see `createModel` for more details).
 */
export class Model {
  /** The model scheduler. */
  private readonly scheduler: ModelScheduler

  /**
   * @hidden This is intended for use by `createModel` only.
   *
   * @param runner The model runner.
   * @param initialOutputs The `Outputs` instance from the initial reference run.
   * @param refData Reference data for a subset of variables.
   */
  constructor(
    private readonly runner: ModelRunner,
    initialOutputs: Outputs,
    private readonly refData: RefDataMap
  ) {
    this.scheduler = new ModelScheduler(runner, initialOutputs)
  }

  /**
   * Run the model (asynchronously) with the inputs configured according to the
   * given scenario and capture the model outputs, which can be accessed later
   * using {@link ModelContext.getSeriesForVar}.
   *
   * Note that currently this method must only be called before the scheduler
   * has started (i.e., at application initialization time); it will throw an
   * error if called after the scheduler has started.
   *
   * @param refScenario The scenario that specifies the input values to be used
   * for the reference run and the outputs to be captured.
   * @return A promise that resolves when the reference run is complete.
   */
  public async addRefData(refScenario: RefScenario): Promise<void> {
    if (this.scheduler.isStarted()) {
      // TODO: For now, treat it as an error if reference runs are requested after
      // the scheduler has already started
      throw new Error('Reference data cannot be added after the scheduler has started')
    }

    await runRefScenario(this.runner, refScenario, this.refData)
  }

  /**
   * Add a new context that holds a distinct set of model inputs and outputs.
   * These inputs and outputs are kept separate from those in other contexts,
   * which allows an application to use the same underlying model to run with
   * multiple I/O contexts.
   *
   * Note that contexts created before the first scheduled model run will
   * inherit the baseline/reference scenario outputs, but as a memory-saving
   * measure, contexts created after that first run will initially have output
   * values set to zero.
   *
   * @param inputs The input values, in the same order as in the spec file passed to `sde`.
   * If undefined, a map of `Input` instances will be initialized according to the input
   * specs in the default `Config`.
   */
  public addContext(inputs?: Map<InputId, Input>): ModelContext {
    return this.scheduler.addContext(inputs, this.refData)
  }
}

/**
 * Create a `ModelRunner` instance that runs the En-ROADS wasm model on the JS thread.
 *
 * This is an asynchronous operation because it loads the wasm model asynchronously.
 */
export async function createDefaultModelRunner(): Promise<ModelRunner> {
  const wasmModule = await loadWasm()
  return createSynchronousModelRunner(wasmModule)
}

/**
 * Create a `Model` instance that uses the given `ModelRunner`.
 *
 * This is an asynchronous operation because it performs an initial
 * model run to capture the reference/baseline data.
 *
 * @param runner The model runner.
 * @param externalData Additional external datasets that will be available for display in graphs.
 */
export async function createModel(runner: ModelRunner, externalData?: RefDataMap): Promise<Model> {
  // We use the default config, which dictates the available model inputs
  // and outputs (and their precise orderings)
  const config = getDefaultConfig()

  // Always perform a primary reference run to capture the baseline ("Ref") data.
  // Note that we save the `Outputs` for this reference run so that initial
  // contexts are initialized with the baseline data.
  const refData: RefDataMap = new Map()
  const refScenario: RefScenario = {
    sourceName: 'Ref',
    inputs: new Map(),
    outputs: [...config.getRefOutputs()]
  }
  const baselineOutputs = await runRefScenario(runner, refScenario, refData)

  // Fold in external data, if provided
  if (externalData) {
    externalData.forEach((value, key) => refData.set(key, value))
  }

  // Create the `Model` instance
  return new Model(runner, baselineOutputs, refData)
}

async function runRefScenario(
  runner: ModelRunner,
  refScenario: RefScenario,
  refData: RefDataMap
): Promise<Outputs> {
  const config = getDefaultConfig()

  // Prepare the inputs
  const inputs: InputValue[] = []
  for (const inputSpec of config.inputs.values()) {
    const scenarioInputValue = refScenario.inputs.get(inputSpec.id)
    let inputValue: number
    if (scenarioInputValue !== undefined) {
      inputValue = scenarioInputValue
    } else {
      inputValue = inputSpec.defaultValue
    }
    inputs.push(createSimpleInputValue(inputSpec.varId, inputValue))
  }

  // Prepare the outputs structure
  const outputs = createDefaultOutputs()

  // Run the model
  await runner.runModel(inputs, outputs)

  // Capture data from the reference run for the given variables; note that we
  // must copy the series data, since the `Outputs` instance can be reused by
  // the runner and otherwise the data might be overwritten
  // TODO: Error if source name is used more than once
  const sourceRefData: Map<OutputVarId, Series> = new Map()
  for (const varId of refScenario.outputs) {
    const refSeries = outputs.getSeriesForVar(varId)
    if (refSeries) {
      sourceRefData.set(varId, refSeries.copy())
    } else {
      // TODO: Return errAsync instead?
      console.error(`ERROR: No reference data available for ${varId}`)
    }
  }
  refData.set(refScenario.sourceName, sourceRefData)

  return outputs
}

/**
 * Create a `ModelRunner` instance that runs the En-ROADS wasm model
 * asynchronously in a Web Worker (in a browser context) or in a worker
 * thread (in a Node.js context).
 *
 * This asynchronous runner reduces the burden on the browser's JavaScript
 * thread and keeps the user interface more responsive when moving sliders, etc.
 *
 * This is an asynchronous operation because it loads the wasm model asynchronously.
 */
export async function createAsyncModelRunner(): Promise<ModelRunner> {
  return spawnAsyncModelRunner({ source: workerJs })
}

/**
 * Create a `Model` instance that runs the En-ROADS wasm model asynchronously
 * in a Web Worker (in a browser context) or in a worker thread (in a Node.js
 * context).
 *
 * This is an asynchronous operation because it performs an initial
 * model run to capture the reference/baseline data.
 */
export async function createAsyncModel(): Promise<Model> {
  // Initialize the asynchronous model runner
  const runner = await createAsyncModelRunner()

  // Create the `Model` instance that uses the asynchronous runner
  return createModel(runner)
}

/**
 * Create a mock model for testing purposes.
 *
 * @hidden
 */
export function createMockModel(): Model {
  // Create a mock model runner that does nothing
  const runner: ModelRunner = {
    createOutputs: () => {
      return createDefaultOutputs()
    },
    runModel: () => {
      return Promise.resolve(undefined)
    },
    terminate: () => {
      return Promise.resolve(undefined)
    }
  }
  return new Model(runner, runner.createOutputs(), new Map())
}
