import { Outputs } from '@sdeverywhere/runtime'

import { startYear, endYear, outputVarIds } from './generated/model-config'

/**
 * Create an `Outputs` instance that is initialized to hold output data
 * produced by the En-ROADS wasm model.
 */
export function createDefaultOutputs(): Outputs {
  const outputs = new Outputs(outputVarIds, startYear, endYear)

  // The "Reduction in GDP vs Temperature" variable is computed in the
  // model as time series data, but it needs to be mapped from [1990, 2100]
  // range to [0, 10] range since it is only ever displayed on a graph with
  // a non-time-series x-axis
  const damageVarId = '_reduction_in_gdp_vs_temperature'
  const damageSeries = outputs.getSeriesForVar(damageVarId)
  if (damageSeries) {
    const span = endYear - startYear
    for (const point of damageSeries.points) {
      // Convert the x value for the point (the year) into the range [0, 10].
      // Note that the model runner only overwrites y values and leaves
      // x values untouched after initialization, so it is safe for us to
      // set them once here.
      const year = point.x
      const x = ((year - startYear) / span) * 10
      point.x = parseFloat(x.toFixed(3))
    }
  } else {
    console.error(`ERROR: No data for damage variable '${damageVarId}'`)
  }

  return outputs
}
