import type { InputValue, InputVarId, ModelRunner } from '@sdeverywhere/runtime'
import { Outputs } from '@sdeverywhere/runtime'

import { getDefaultConfig } from '../config'

/**
 * Holds values related to the social cost of carbon.
 */
export interface SocialCostOutputs {
  /** The "Social Cost of Carbon" value. */
  socialCostOfCarbon: number
}

/**
 * Coordinates special model runs to the year 2300 and computes values related to
 * the social cost of carbon.
 *
 * @hidden This is intended for use by `ModelScheduler` only.
 */
export class SocialCostScheduler {
  /** The second set of inputs used for special model runs to 2300. */
  private readonly specialInputs: InputValue[]

  /** The `Outputs` instance used for special model runs; only contains values for 2300. */
  private specialOutputs: Outputs

  /** The input used to control "FINAL TIME" for the special model runs. */
  private readonly finalTimeInput: InputValue

  /** The input used to control "SWITCH SCC" for the special model runs. */
  private readonly socialCostInput: InputValue

  /** The input used to control the "emission shock" scenario (the second special model run). */
  private readonly emissionShockInput: InputValue

  /**
   * @param runner The model runner.
   */
  constructor(private readonly runner: ModelRunner) {
    const config = getDefaultConfig()

    // Include the given damage variables plus the special control inputs
    const specialVarIds: Set<InputVarId> = new Set()
    for (const varId of config.getDamageInputs()) {
      specialVarIds.add(varId)
    }
    const finalTimeVarId = '_final_time'
    const sccSwitchVarId = '_switch_scc'
    const shockVarId = '_pv_of_cumulative_damage_by_the_final_time_in_the_reference_simulation'
    specialVarIds.add(finalTimeVarId)
    specialVarIds.add(sccSwitchVarId)
    specialVarIds.add(shockVarId)

    // Create an array for holding the inputs for the special model runs
    this.specialInputs = []
    for (const regularInput of config.inputs.values()) {
      const varId = regularInput.varId
      const defaultValue = regularInput.defaultValue
      const acceptSet = specialVarIds.has(varId)
      const specialInput = createSpecialInputValue(varId, defaultValue, acceptSet)
      this.specialInputs.push(specialInput)
    }

    // Isolate some inputs to make them easier to set
    this.finalTimeInput = this.specialInputs.find(input => input.varId === finalTimeVarId)
    this.socialCostInput = this.specialInputs.find(input => input.varId === sccSwitchVarId)
    this.emissionShockInput = this.specialInputs.find(input => input.varId === shockVarId)
  }

  /**
   * Run the model to the year 2300 and compute the social cost of carbon values.
   *
   * @param currentInputs The current set of model inputs.
   * @param regularOutputs The outputs of the regular model run to 2100.
   */
  async runModel(currentInputs: InputValue[], regularOutputs: Outputs): Promise<SocialCostOutputs> {
    // Create the special outputs lazily (so that they don't take up space if
    // the social cost view is not used)
    if (this.specialOutputs === undefined) {
      // Use the regular outputs to create a second `Outputs` instance that will be
      // used for the special model runs; this one will hold the full set of output
      // variables, but it only needs to contain data for a single year (2300).
      // Note that the C code is modified at build time to include custom code for
      // when "FINAL TIME" is set to 2300; in that mode, the model only saves data
      // for the year 2300, and it saves it in the first time column.  Therefore,
      // the model runner will copy just that year's worth of data into the
      // `Outputs` instance created here.
      this.specialOutputs = new Outputs(regularOutputs.varIds, 2300, 2300)
    }

    const specialValueIn2300 = (varId: string) => {
      return this.specialOutputs.getSeriesForVar(varId).getValueAtTime(2300)
    }

    // Set the special inputs.  This will only update the inputs that are allowed to
    // be included in the special runs; all others will maintain their default value.
    for (let i = 0; i < this.specialInputs.length; i++) {
      this.specialInputs[i].set(currentInputs[i].get())
    }

    // Override the special "FINAL TIME" value so that the model runs to 2300
    this.finalTimeInput.set(2300)

    // Run the model to 2300 with the "damage baseline" scenario (set "SWITCH SCC" to 0)
    this.socialCostInput.set(0)
    this.emissionShockInput.set(0)
    this.specialOutputs = await this.runner.runModel(this.specialInputs, this.specialOutputs)

    // Capture the "present value" output from the first special (damage baseline) run;
    // this will be used as an input for the second special (emission shock) run
    const damageValue = specialValueIn2300('_present_value_of_cumulative_damage_by_final_time')

    // Run the model to 2300 with the "emission shock" scenario (set "SWITCH SCC" to 1
    // and set the "PV of cumulative damage" variable to the output of the first run)
    this.socialCostInput.set(1)
    this.emissionShockInput.set(damageValue)
    this.specialOutputs = await this.runner.runModel(this.specialInputs, this.specialOutputs)

    // Capture the "social cost of carbon" output from the second special (emission shock) run
    const socialCostOfCarbon = specialValueIn2300('_social_cost_of_carbon')

    return {
      socialCostOfCarbon
    }
  }
}

/**
 * Create an `InputValue` that is only used for special model runs to 2300.  This is
 * a simpler implementation that does not notify any callbacks.
 *
 * @hidden
 *
 * @param varId The input variable ID.
 * @param defaultValue The default value of the variable.
 * @param acceptSet If true, accept the value passed to `set`, otherwise keep
 * the default value that was captured when this input was created.
 */
function createSpecialInputValue(
  varId: InputVarId,
  defaultValue: number,
  acceptSet: boolean
): InputValue {
  let currentValue = defaultValue

  const get = () => {
    return currentValue
  }

  const set = (newValue: number) => {
    if (acceptSet) {
      currentValue = newValue
    }
  }

  const reset = () => {
    set(defaultValue)
  }

  return { varId, get, set, reset, callbacks: {} }
}
