import type { InputValue, ModelRunner, Outputs } from '@sdeverywhere/runtime'

import type { InputId } from '@climateinteractive/sim-core'

import { getDefaultConfig } from '../config'

import { inputVarIds } from './generated/model-config'

import { ModelContext } from './context'
import type { Input } from './inputs'
import { createModelInput, createSimpleInputValue } from './inputs'
import { createDefaultOutputs } from './outputs'
import type { RefDataMap } from './ref-data'
import { SocialCostScheduler } from './social-cost'

/**
 * A high-level interface that schedules running of the underlying `WasmModel`.
 *
 * When one or more input values are changed, this class will schedule a model
 * run to be completed as soon as possible.  When the model run has completed,
 * `onOutputsChanged` is called to notify that new output data is available.
 *
 * The `ModelRunner` is pluggable to allow for running the model synchronously
 * (on the main JavaScript thread) or asynchronously (in a Web Worker or Node.js
 * worker thread).
 *
 * @hidden This is intended for use by `Model` only.
 */
export class ModelScheduler {
  /** The sub-scheduler for special model runs to compute social cost of carbon. */
  private readonly socialCostScheduler: SocialCostScheduler

  /** The second array that holds a stable copy of the user inputs. */
  private readonly currentInputs: InputValue[]

  /** The contexts that hold distinct sets of inputs and outputs. */
  private readonly contexts: ModelContext[] = []

  /** Whether a model run has been scheduled. */
  private runNeeded = false

  /** Whether a model run is in progress. */
  private runInProgress = false

  /**
   * @param runner The model runner.
   * @param initialOutputs The `Outputs` instance from the initial reference run.
   */
  constructor(
    private readonly runner: ModelRunner,
    private initialOutputs: Outputs
  ) {
    // Create the separate social cost of carbon scheduler
    this.socialCostScheduler = new SocialCostScheduler(runner)

    // Create a second array to hold a stable copy of the user inputs during model runs
    const config = getDefaultConfig()
    this.currentInputs = []
    for (const inputSpec of config.inputs.values()) {
      this.currentInputs.push(createSimpleInputValue(inputSpec.varId))
    }
  }

  /**
   * Return true if the scheduler has started any model runs.
   */
  public isStarted(): boolean {
    return this.initialOutputs === undefined
  }

  /**
   * Add a new context that holds a distinct set of model inputs and outputs.
   * These inputs and outputs are kept separate from those in other contexts,
   * which allows an application to use the same underlying model to run with
   * multiple I/O contexts.
   *
   * Note that contexts created before the first scheduled model run will
   * inherit the baseline/reference scenario outputs, but as a memory-saving
   * measure, contexts created after that first run will initially have output
   * values set to zero.
   *
   * @param inputs The input values, in the same order as in the spec file passed to `sde`.
   * If undefined, a map of `Input` instances will be initialized according to the input
   * specs in the default `Config`.
   * @param refData Reference data for a subset of variables.
   */
  public addContext(inputs: Map<InputId, Input> | undefined, refData: RefDataMap): ModelContext {
    let orderedInputs: Map<InputId, Input>
    if (inputs !== undefined) {
      // Ensure that the inputs are in the order expected by the wasm model
      orderedInputs = getOrderedInputs(inputs)
    } else {
      // Create a default `Input` instance for each input variable in the config
      const config = getDefaultConfig()
      orderedInputs = new Map()
      for (const inputSpec of config.inputs.values()) {
        const input = createModelInput(inputSpec)
        orderedInputs.set(input.spec.id, input)
      }
    }

    // If we haven't yet performed the initial scheduled model run, use the output
    // data from the baseline/reference run, otherwise initialize a new instance
    let outputs: Outputs
    if (this.initialOutputs !== undefined) {
      if (this.contexts.length === 0) {
        // For the first context, use the initial outputs by reference
        outputs = this.initialOutputs
      } else {
        // For all other contexts, create a copy of the initial outputs
        outputs = createDefaultOutputs()
        for (const varId of outputs.varIds) {
          const series0 = this.initialOutputs.getSeriesForVar(varId)
          const series1 = outputs.getSeriesForVar(varId)
          for (let i = 0; i < series0.points.length; i++) {
            series1.points[i].y = series0.points[i].y
          }
        }
      }
    } else {
      // Initialize a new instance (with values set to zero)
      outputs = createDefaultOutputs()
    }

    // Create the context
    const context = new ModelContext(orderedInputs, outputs, refData)

    // If social cost runs are activated, schedule a model run
    context.onSocialCostActiveChanged = active => {
      if (active) {
        context.runNeeded = true
        this.runWasmModelIfNeeded()
      }
    }

    // When any input has an updated value, schedule a model run on the next tick
    const afterSet = () => {
      context.runNeeded = true
      this.runWasmModelIfNeeded()
    }
    for (const input of orderedInputs.values()) {
      input.callbacks.onSet = afterSet
    }

    // Add to the set of contexts managed by the scheduler
    this.contexts.push(context)

    return context
  }

  /**
   * Schedule a wasm model run (if not already pending).  When the run is
   * complete, save the outputs and call the `onOutputsChanged` callback.
   */
  private runWasmModelIfNeeded(): void {
    // Set a flag indicating that a new run is needed (even if one is already
    // in progress)
    this.runNeeded = true

    if (this.runInProgress) {
      // A run is already in progress; let it finish first
      return
    } else {
      // A run is not already in progress, so schedule it now.  We use
      // `setTimeout` so that if a lot of inputs are all changing at once
      // (like after a reset), we wait for all those `set` or `reset`
      // calls to finish before gathering the input values into an array
      // and initiating the run on the next tick.
      this.runInProgress = true
      setTimeout(() => {
        // Kick off the (possibly asynchronous) model run
        this.runWasmModelNow()
      }, 0)
    }
  }

  /**
   * Run the wasm model asynchronously for all relevant contexts.
   */
  private async runWasmModelNow(): Promise<void> {
    // Invalidate the initial outputs
    this.initialOutputs = undefined

    // Run the model for each context that requested a run
    for (const context of this.contexts) {
      if (context.runNeeded) {
        context.runNeeded = false
        await this.runWasmModelNowForContext(context)
      }
    }

    // See if another run is needed
    if (this.runNeeded) {
      // Keep `runInProgress` set, but clear the `runNeeded` flag
      this.runNeeded = false
      setTimeout(() => {
        this.runWasmModelNow()
      }, 0)
    } else {
      // No run needed, so clear both flags
      this.runNeeded = false
      this.runInProgress = false
    }
  }

  /**
   * Run the wasm model asynchronously using the current set of input values in the given context.
   *
   * @param context The context to use for the model run.
   */
  private async runWasmModelNowForContext(context: ModelContext): Promise<void> {
    // Copy the current inputs into a separate array; this ensures that the
    // following model runs all use the same inputs, even if the user continues
    // to change the inputs while the model is being run asynchronously
    for (let i = 0; i < context.inputsArray.length; i++) {
      this.currentInputs[i].set(context.inputsArray[i].get())
    }

    // Run the model with the current set of input values and save the outputs
    try {
      // Perform the "regular" model run to 2100
      await this.runner.runModel(this.currentInputs, context.outputs)

      if (context.isSocialCostActive()) {
        // Perform the special model runs to 2300 to compute social cost of carbon
        const sccOutputs = await this.socialCostScheduler.runModel(
          this.currentInputs,
          context.outputs
        )

        // Notify that the values have been updated
        context.onSocialCostOutputsChanged?.(sccOutputs)
      }

      // Notify that the regular outputs have been updated
      context.onOutputsChanged?.()
    } catch (e) {
      console.error('ERROR: The scheduler encountered an error when running the model:', e)
    }
  }
}

/**
 * Ensure that the inputs are in the order expected by the wasm model.
 * If the size of the given map is incorrect, will throw an error.
 *
 * @hidden
 */
function getOrderedInputs(inputMap: Map<InputId, Input>): Map<InputId, Input> {
  if (inputMap.size !== inputVarIds.length) {
    throw new Error(
      `Number of inputs (${inputMap.size}) does not match list expected by model (${inputVarIds.length})`
    )
  }

  const unorderedInputs = Array.from(inputMap.values())
  const orderedInputs: Map<InputId, Input> = new Map()
  for (const inputVarId of inputVarIds) {
    const input = unorderedInputs.find(i => i.varId === inputVarId)
    if (!input) {
      throw new Error(`No model input found for ${inputVarId}`)
    }
    orderedInputs.set(input.spec.id, input)
  }
  return orderedInputs
}
