import type { Outputs } from '@sdeverywhere/runtime'
import { Series } from '@sdeverywhere/runtime'

import type { InputId, OutputVarId } from '@climateinteractive/sim-core'

import type { Input } from './inputs'
import type { RefDataMap, SourceName } from './ref-data'
import type { SocialCostOutputs } from './social-cost'

import { staticData } from './generated/static-data'

/**
 * Defines a context that holds a distinct set of model inputs and outputs.
 * These inputs and outputs are kept separate from those in other contexts,
 * which allows an application to use the same underlying model to run with
 * multiple I/O contexts.
 */
export class ModelContext {
  /**
   * A copy of the values of the `inputs` map in array form for easier/faster linear access.
   * @hidden This is intended for use by `ModelScheduler` only.
   */
  public readonly inputsArray: Input[]

  /**
   * The structure into which the model outputs will be stored.
   * @hidden This is intended for use by `ModelScheduler` only.
   */
  public readonly outputs: Outputs

  /**
   * Whether a model run is needed for this context.
   * @hidden This is intended for use by `ModelScheduler` only.
   */
  public runNeeded = false

  /** Whether to enable special model runs to 2300. */
  private socialCostActive = false

  /**
   * Called when the outputs have been updated after a model run.
   */
  public onOutputsChanged?: () => void

  /**
   * Called when the "Social Cost of Carbon" values have been updated after special
   * model runs.
   */
  public onSocialCostOutputsChanged?: (outputs: SocialCostOutputs) => void

  /**
   * Called when the `socialCostActive` flag is changed.
   * @hidden For internal use only.
   */
  public onSocialCostActiveChanged?: (active: boolean) => void

  /**
   * @hidden This is intended for use by `Model` only.
   *
   * @param inputs The input values, in the same order as in the spec file passed to `sde`.
   * @param outputs The structure into which the model outputs will be stored.
   * @param refData Reference data for a subset of variables.
   */
  constructor(
    private readonly inputs: Map<InputId, Input>,
    outputs: Outputs,
    private readonly refData: RefDataMap
  ) {
    this.inputsArray = Array.from(inputs.values())
    this.outputs = outputs
  }

  /**
   * Return the model input for the given input ID, or undefined if there is
   * no input for that ID.
   */
  public getInputForId(inputId: InputId): Input | undefined {
    return this.inputs.get(inputId)
  }

  /**
   * Return the series data for the given model output variable.
   *
   * @param varId The ID of the output variable associated with the data.
   * @param sourceName The external data source name (e.g. "Ref"), or
   * undefined to use the latest model output data.
   */
  public getSeriesForVar(varId: OutputVarId, sourceName?: SourceName): Series | undefined {
    if (sourceName === undefined) {
      // Return the latest model output data
      return this.outputs.getSeriesForVar(varId)
    } else {
      const refDataForSource = this.refData.get(sourceName)
      if (refDataForSource !== undefined) {
        // Return the saved reference data
        return refDataForSource.get(varId)
      } else {
        // Return the static external data
        const dataset = staticData[sourceName]
        if (dataset) {
          const points = dataset[varId]
          if (points) {
            return new Series(varId, points)
          }
        }
        return undefined
      }
    }
  }

  /**
   * Return whether the special "Social Cost of Carbon" runs are enabled.
   */
  public isSocialCostActive(): boolean {
    return this.socialCostActive
  }

  /**
   * Set whether the "Social Cost of Carbon" value should be computed by
   * running the model to 2300.
   *
   * @param active Whether special model runs are needed.
   */
  public setSocialCostActive(active: boolean): void {
    if (active !== this.socialCostActive) {
      this.socialCostActive = active
      this.onSocialCostActiveChanged?.(active)
    }
  }
}
